import React from 'react';
import {Route, Switch} from "react-router-dom";
import AddOrderWelcome from "./AddOrderWelcome";
import AddCompany from "./AddCompany";

function AddOrder() {
  return (
    <div>
      <Switch>
        <Route path="/dashboard/add" exact component={AddOrderWelcome}/>
        <Route path="/dashboard/add/company" component={AddCompany}/>
      </Switch>
    </div>
    
  )
}

export default AddOrder;
