import warningImage from "../assets/images/warning.svg";
import React from "react";
import Label from "arui-feather/label";

function ErrorBlock({title=null, message=null, className="", body: Component}) {
  return (
    <div className={"error-block " + className}>
      <div className="d-block d-md-flex text-left">
        <img src={warningImage} alt="" height="50px" className="d-inline-block mx-3 ml-md-0"/>
        <div>
          {!!title && <Label className="d-block my-2" theme="alfa-on-color" size="l"><span className="font-bold">{title}</span></Label>}
          {!!message && <Label className="d-block my-3" theme="alfa-on-color" size="m">{message}</Label>}
          {!!Component && <Component/>}
        </div>
      </div>
    </div>
  )
}

export default ErrorBlock;
