import React from "react";
import Heading from "arui-feather/heading";
import Button from "arui-feather/button";
import Form from "arui-feather/form";
import FormField from "arui-feather/form-field";
import Input from "arui-feather/input";
import Label from "arui-feather/label";
import addCompanyService from "../_services/addCompany.service";
import {BarLoader} from "react-spinners";

class SelectCompanyTypeForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      type: null,
      iin: '',
      status: 'clean',
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  render() {
    const {type, iin, status} = this.state;
    const {className=''} = this.props;
    return (
      <div className={className} style={{minHeight: '500px'}}>
        <Heading size="m" className="text-center">Выберите тип вашей компании</Heading>
        <div className="row">
          <div className="col-sm-6 text-center text-md-right mb-4">
            <Button ty onClick={() => this.setState({type: 'ИП'})} className={"button_width_150 " + (type==='ИП' ? 'blued' : '')}>ИП</Button>
          </div>
          <div className="col-sm-6 text-center text-md-left mb-4">
            <Button onClick={() => this.setState({type: 'ТОО'})} className={"button_width_150 " + (type==='ТОО' ? 'blued' : '')}>ТОО</Button>
          </div>
        </div>
        { type && (
          <div className="d-block mx-auto my-5" style={{width: '300px'}}>
            <Form className="fade_in-to_top" onSubmit={this.handleSubmit}>
              <FormField className="pb-3">
                <Label>{type==='ИП' ? "Введите ваш ИИН" : "Введите ваш БИН"}</Label>
                <Input width="available"
                       mask="111 111 111 111"
                       placeholder="### ### ### ###"
                       value={iin}
                       onChange={(iin) => this.setState({iin})}
                />
              </FormField>
              <div className="text-right">
                <div className="d-inline-block" style={{width: '100px'}}>
                  <Button type="submit" size="s" width="available" disabled={!this.isValid || status === 'loading'}>
                    { status !== 'loading' && "Отправить" }
                    { status === 'loading' &&  <BarLoader width={60}/>}
                  </Button>
                </div>
              </div>
            </Form>
          </div>
        )}
      </div>
    )
  }
  
  get isValid() {
    const n = this.state.iin.match(/\d+/g) ? this.state.iin.match(/\d+/g) : [];
    return n.join('').length === 12;
  }
  
  handleSubmit() {
    const n = this.state.iin.match(/\d+/g) ? this.state.iin.match(/\d+/g) : [];
    const iin = n.join('');
    const type = this.state.type;
    this.setState({status: 'loading'});
    addCompanyService.checkIIN(iin).then(data => {
      this.setState({status: 'success'});
      this.props.onSuccess({...data, iin, type});
      
    }).catch(err => {
      this.setState({status: 'error'});
      this.props.onError({...err});
    });
  }
}

SelectCompanyTypeForm.defaultProps = {
  onSuccess: () => {},
  onError: () => {},
};

export default SelectCompanyTypeForm;
