import React from 'react';
import Heading from "arui-feather/heading";
import CityImage from "../../assets/images/city.image.svg";
import Button from "arui-feather/button";
import SelectCompanyTypeForm from "../../forms/SelectCompanyTypeForm";
import ErrorBlock from "../../components/ErrorBlock";
import Label from "arui-feather/label";
import {NavLink} from "react-router-dom";
import {ACCOUNT_FORM, SET_CLIENT_TYPE} from "../../_types";
import {connect} from "react-redux";

class AddOrderWelcome extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      started: 0,
      page: 0,
      data: null,
      error: null
    };
    
    this.openForm = this.openForm.bind(this);
    this.handleSuccess = this.handleSuccess.bind(this);
    this.handleError = this.handleError.bind(this);
  }
  
  openForm(val=1) {
    console.log(val);
    this.setState({started: val});
    setTimeout(() => {
      this.setState({page: val});
    }, 300);
  }
  
  handleSuccess(data) {
    this.openForm(2);
    this.setState({data})
  }
  
  handleError(error) {
    this.openForm(3);
    this.setState({error});
    console.log(error);
  }
  
  render() {
    const {page, started, data, error} = this.state;
    return (
      <div className="add-order-page">
        <Heading size="xl" className="mt-3 text-center">Онлайн открытие счета</Heading>
        { page === 0 && (
          <div className={started===0 ? "fade_in-to_left" : "fade_out-to_left"}>
            <div className="text-center"><img src={CityImage} alt=""/></div>
            <div className="text-center">
              <div className="inherit mx-2">
                <Button onClick={() => this.openForm()}>Открыть счет</Button>
              </div>
            </div>
          </div>
        )}
        { page === 1 && (
          <SelectCompanyTypeForm className={started===1 ? 'fade_in-to_left' : 'fade_out-to_left'} onSuccess={this.handleSuccess} onError={this.handleError}/>
        )}
        { page === 2 && (
          <div className={'text-center ' + (started === 2 ? 'fade_in-to_left' : 'fade_out-to_left')}>
            <Label className="d-block my-3 opacity-middle">Это ваша компания?</Label>
            <Label size="xl">«{data.name}»</Label>
            <div className="text-center my-3">
              <Button onClick={() => this.openForm(1)} className="mx-2">Нет</Button>
              <NavLink to="/dashboard/add/company/base" className="d-inline-block inherit mx-2">
                <Button onClick={() => this.props.setClientType(data.type)} view="extra">Да</Button>
              </NavLink>
            </div>
          </div>
        )}
        { page === 3 && (
          <div className={started === 3 ? 'fade_in-to_left' : 'fade_out-to_left'}>
            <ErrorBlock className="my-5" title="Ошибка при проверке" message={error.message}/>
            <div className="text-center">
              <Button onClick={() => this.openForm(1)} className="mx-2">Вернуться назад</Button>
            </div>
          </div>
        )}
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  setClientType: (payload) => dispatch({type: ACCOUNT_FORM+SET_CLIENT_TYPE, payload})
});

export default connect(null, mapDispatchToProps)(AddOrderWelcome);
